*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, body{
    color: white;
    background-color: #03031c;
    font-family: Lato, sans-serif;
    margin-top: 40px;
}
h1{
  color: #1b1b32;
  font-size: 25px;
  font-family: Roboto, sans-serif;
  text-align: center;
  margin: 20px 0;   
}
.App{
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: center;

}
.freecodecamp-logo-contenedor{
